.news-post {
  display: block;
  position: relative;
  margin-bottom: 40px;
  color: $gray-700;
  text-decoration: none;

  &__image {
    position: absolute;
    top: 50%;
    left: 50%;
    max-width: 100%;
    width: 100%;
    max-height: 100%;
    height: 100%;
    object-fit: cover;
    transform: translate(-50%, -50%);

    &-wrapper {
      position: relative;
      width: 100%;
      margin-bottom: 25px;
      padding-top: 75%;
      overflow: hidden;
    }
  }

  &__date {
    display: block;
    margin-bottom: 15px;
    font-size: 13px;
  }

  &__name {
    display: block;
    max-width: 240px;
    margin-bottom: 20px;
    font-size: 17px;
    font-weight: 600;
  }

  &__description {
    display: none;
    position: relative;
    height: 72px;
    padding-right: 0.65em;
    font-size: 15px;
    line-height: 1.6;
    overflow: hidden;
  }

  &_big {
    .news-post__name {
      max-width: 410px;
      margin-bottom: 20px;
      font-size: 24px;
      line-height: 1.33;
    }

    .news-post__description {
      display: block;
    }
  }

  &:hover,
  &:focus {
    .news-post__name {
      text-decoration: underline;
    }
  }
}

@media screen and (max-width: 1199px) {
  .news-post {
    &_big {
      .news-post__name {
        margin-bottom: 0;
        font-size: 17px;
      }

      .news-post__description {
        display: none;
      }
    }
  }
}

@media screen and (max-width: 767px) {
  .news-post {
    &__name {
      font-size: 15px;
    }

    &_big {
      .news-post__name {
        font-size: 15px;
      }
    }
  }
}

@media screen and (max-width: 575px) {
  .news-post {
    margin-bottom: 35px;
  }
}
